*{
  font-family: Arial, Helvetica, sans-serif  ;
}
body{
  padding: 60px;
}
input{
  width: 100%;
  font-size: 1.2rem;
  color:grey;
}
.ard-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  
}
.card{
  align-content: center;
  border: 1-x solid gray;
  border-radius: 6px;
  margin-bottom:10px ;
  margin-right:10px;
  overflow: hidden;
}
.card p {
  padding: 5px;
  

}
.card img{
  max-height: 170px;
}